import {LitElement, html, css} from 'lit';
import {customElement, property} from 'lit/decorators.js';
import accordionStyle from "./style.scss";


@customElement('t3-accordion-item')
export class T3AccordionItem extends LitElement {

  static styles = [ css`:host{display: block;}`, accordionStyle]

  render() {
    return html`<slot></slot>`
  }
}

@customElement('t3-accordion-header')
export class T3AccordionHeader extends LitElement {

  static styles = [css`
    :host:after {
      content: '\\002B';
      color: #777;
      font-weight: bold;
    }
    
    :host([active]):after {
      content: "\\2212";
    }`,  accordionStyle]

  @property({type: CustomEvent}) clickHeader = new CustomEvent('clickHeader', {
    detail: {message: 'clickHeader happened.'},
    bubbles: true,
    composed: true
  });

  @property({
    type: Boolean,
    attribute: true,
    reflect: true,
  }) active = false;

  constructor() {
    super();
    this.addEventListener('click', this.handleClick);
  }

  handleClick() {
    this.dispatchEvent(this.clickHeader);
  }

  render() {
    return html`<slot class="card-header">Default header</slot>`
  } // <div class="card-header"></div>

  attributeChangedCallback(name, oldval, newval) {
    console.log('attribute change: ', name, oldval, newval);

    if (this.hasAttribute('active')) {
      this.nextElementSibling.style.maxHeight = this.nextElementSibling.scrollHeight + "px";
    } else {
      this.nextElementSibling.style.maxHeight = null;
    }
    super.attributeChangedCallback(name, oldval, newval);
  }
}


@customElement('t3-accordion-detail')
export class T3AccordionDetail extends LitElement {
  @property({type: Boolean, reflect: true}) selected = false;

  static styles = [ css`:host{
    display: block;
    padding: 0 18px;
    background-color: white;
    max-height: 0px;
    overflow: hidden;
    transition: max-height 0.2s ease-out;
  }`, accordionStyle]

  render() {
    return html`<slot class="card-body">Default details</slot>`
  }
}
