import {LitElement, html, css} from 'lit';
import {customElement, property} from 'lit/decorators.js';
import accordionStyle from './style.scss';

/**
 * `<t3-accordion>` is accordion component. use always to persist opening state
 * Example:
 *  ```
 *  <t3-accordion always="true">
 *    <t3-accordion-item>
 *      <t3-accordion-header>Accordion Title</t3-accordion-header>
 *      <t3-accordion-detail>
 *        <p>Any accordion contents</p>
 *      </t3-accordion-detail>
 *    </t3-accordion-item>
 *   </t3-accordion>
 *  ```
 */

@customElement('t3-accordion')
class T3Accordion extends LitElement {
  @property({type: Boolean}) always = false;

  static styles = [ css`:host{display: block;}`, accordionStyle]

  render() {
    return html`<slot></slot>`;
  }

  get accordions(): Element[] {
    const slot = this.shadowRoot.querySelector('slot');
    return slot ? slot.assignedElements(): [];
  }

  constructor() {
    super();
    this.addEventListener('clickHeader', this.manageSpoilers)
  }

  manageSpoilers(ev) {
    ev.target.toggleAttribute('active');
    const active = this.querySelectorAll('t3-accordion-header[active]');

    if(this.always !== true) {
      active.forEach(function (el) {
        if (el !== ev.target)
          el.removeAttribute('active');
      });
    }

  }
}
